(function() {
    'use strict';

    angular
        .module('cure')
        .controller('AuthController', function($location, $log, $scope, Discog, Auth, toastr){

            var vm = this;
            vm.oauthLogin = oauthLogin;
            vm.user = null;
            // Show top X by votes : will update live as/when top X changes
            vm.topTracks = Discog.getRanks(5);

            function oauthLogin(providerName) {
                if(!providerName){
                    return;
                }
                Auth.$signInWithPopup(providerName)
                    .then(function(result) {
                        $location.path('/release');
                    }).catch(function(reason) {
                        $log.warn('error ::: ', reason);
                        toastr.error(reason.statusText, 'Error', {
                            onHidden: function(){
                                $location.path('/');
                            }
                        });
                    });
            }

            Auth.$onAuthStateChanged(function(user) {
                vm.user = user;
            });

            // To clean up angularfire references (stop permission errors...)
            $scope.$on('$destroy', function() {
                vm.topTracks.$destroy && vm.topTracks.$destroy();
            });

        });

})();

(function() {
    'use strict';

    angular
        .module('cure')
        .run(runBlock);

    function runBlock($log, $window, $rootScope, $location) {

        var r = $rootScope.$on('$routeChangeError', function(event, next, previous, error) {
            // We can catch the error thrown when the $requireSignIn promise is rejected
            // and redirect the user back to the home page
            if (error === 'AUTH_REQUIRED') {
                $location.path('/');
            }
        });

    }

})();

(function() {
    'use strict';

    angular
        .module('cure')
        .config(routeConfig);

    function routeConfig($routeProvider) {

        $routeProvider.whenAuthenticated = function (path, route) {
            route.resolve = route.resolve || {};
            route.resolve.user = ['Auth', function (Auth) {
                return Auth.$requireSignIn();
            }];
            $routeProvider.when(path, route);
            return this;
        }

        $routeProvider
            .when('/', {
                templateUrl: 'app/auth/login.html',
                controller: 'AuthController',
                controllerAs: 'vm',
                resolve: {
                    requireNoAuth: ['$location', 'Auth', function($location, Auth){
                        return Auth.$requireSignIn().then(function(auth){
                            $location.path('/discog');
                        }, function(error){
                            return;
                        });
                    }]
                }
            })
            .when('/about', {
                templateUrl: 'app/about/about.html'
            })
            .whenAuthenticated('/discog', {
                templateUrl: 'app/discog/discog.html',
                controller: 'DiscogController',
                controllerAs: 'vm'
            })
            .whenAuthenticated('/discog/:id', {
                templateUrl: 'app/release/release.html',
                controller: 'ReleaseController',
                controllerAs: 'vm'
            })
            .whenAuthenticated('/discog/:id/:track', {
                templateUrl: 'app/track/track.html',
                controller: 'TrackController',
                controllerAs: 'vm'
            })
            .otherwise({
                redirectTo: '/'
            });
        }

})();

(function() {
    'use strict';

    angular
        .module('cure')
        .config(config);

    function config($logProvider, toastrConfig) {

        $logProvider.debugEnabled(true);

        toastrConfig.allowHtml = true;
        toastrConfig.timeOut = 2000;
        toastrConfig.positionClass = 'toast-top-right';
        toastrConfig.preventDuplicates = false;     // true prevents same as last toast : don't want
        toastrConfig.preventOpenDuplicates = true;  // true prevents the same toast as is currently showing
        toastrConfig.maxOpened = 1;
        toastrConfig.progressBar = true;

    }

})();

